export default {
	data() {
		return {
			tabbar: [{
				title: '首页',
				icon: 'home',
				choosed: false,
				url: '/pages/catalog/catalog'
			},{
				title: '课程',
				icon: 'search',
				choosed: false,
				url: '/pages/lessons/lessons'
			},{
				title: '店铺',
				icon: 'shop',
				choosed: true,
				url: '/pages/store/store'
			},{
				title: '我的',
				icon: 'people',
				choosed: false,
				url: '/pages/me/me'
			}],
		}
	},
	onLoad() {
		uni.showLoading({
			title: '加载中...',
			mask: true
		});
	},
	onReady() {
		uni.hideLoading()
	},
	methods: {
		jumpTo(e, url){
			uni.navigateTo({
				url: url
			})
		},
		switchTo(e, url) {
			uni.redirectTo({
				url: url
			})
		},
	}
}
